import { isArray } from 'underscore';
import { getTableMatrix, CELL_TYPE, getRealIndex } from 'utils/Table';

import Backbone from 'backbone';

var $ = Backbone.$;

export default {
  run(ed, sender, opts = {}) {

    let components = opts.component || ed.getSelectedAll();
    components = isArray(components) ? [...components] : [components];
    if (components.length > 1) {
      return
    }

    // component
    var component = this.em.getSelected()
    if (!component) {
      return
    }

    // matrix
    let view = component.view;
    let $td = $(view.el);
    let $tr = $td.closest('tr');
    let tr_index = $tr.index();
    let $table = $td.closest('table');
    let $trs = $table.find('tr');
    let matrix = getTableMatrix($table);

    // find the virtual index
    let virtual_col_index = -1;
    let matrix_row = matrix[tr_index];
    for (var i = 0; i < matrix_row.length; i++) {
      let cell_info = matrix_row[i];
      if (cell_info.type == CELL_TYPE.CELL
        && cell_info.cell == $td[0]) {
        virtual_col_index = i;
        break;
      }
    }

    // virtual_col_index is the virtual index of the cell
    if (virtual_col_index == -1) {
      return
    }

    // update the matrix
    for (var row_index = 0; row_index < matrix.length; row_index++) {
      let $row = $($trs[row_index]);
      let row_model = $row.data('model');
      let matrix_row = matrix[row_index];
      let cell_info = matrix_row[virtual_col_index];
      let real_index = getRealIndex(matrix_row, virtual_col_index);
      let related_cell = cell_info.cell;
      if (cell_info.type === CELL_TYPE.COL_SPAN) {
        let colSpan = cell_info.colSpan;
        related_cell.colSpan = colSpan - 1;
      } else if (cell_info.type === CELL_TYPE.COL_ROW_SPAN
        && cell_info.row_index === cell_info.related_cell_row_index) {
        // add the colSpanas we add a column
        related_cell.colSpan = related_cell.colSpan - 1;
        // keep the rowspan
      } else if (cell_info.type == CELL_TYPE.CELL) {
        // if the cell has colSpan, we must remove the colSpan
        if (cell_info.colSpan > 1) {
          let tmp_cell_info = {
            type: 'cell',
            attributes: {
              rowSpan: cell_info.rowSpan,
            }
          }
          // col span
          if (cell_info.colSpan > 1) {
            tmp_cell_info.attributes.colSpan = cell_info.colSpan - 1;
          }
          // just keep the columnspan
          row_model.append(tmp_cell_info, {
            at: real_index
          })
        }
        let cell = cell_info.cell;
        let model = $(cell).data('model');
        if (model) {
          model.remove();
        }
      }
    }
  }
};
