import ComponentView from './ComponentView';

import { bindAll } from 'underscore';
const $ = Backbone.$;

import { getTableMatrix } from 'utils/Table';

export default ComponentView.extend({

    init() {
        // Bind all functions from this object to the component view
        ComponentView.prototype.init.apply(this, arguments);

        // bind the event handlers to the component view
        bindAll(this, 'on_change_col_span', 'on_change_row_span');

        // listen for the attribute change
        this.listenTo(this.model, 'change:attributes:colspan', this.on_change_col_span);
        this.listenTo(this.model, 'change:attributes:rowspan', this.on_change_row_span);
    },

    get_col_index(td) {
        let tr = $(td).closest('tr');
        let tds = $(tr).find('td');
        let index = 0;
        for (let i = 0; i < tds.length; i++) {
            if (tds[i] === td) {
                return index;
            }
            // add the colspan of the current td
            index += $(tds[i]).attr('colspan') || 1;
        }
    },

    /**
     * get the cell equal to the index
     * @param {*} index 
     * @returns 
     */
    get_index_cell(index) {
        let tr = $(td).closest('tr');
        let tds = $(tr).find('td');
        let tmp_index = 0;
        for (let i = 0; i < tds.length; i++) {
            if (index === tmp_index) {
                return tds[i];
            }
            // add the colspan of the current td
            tmp_index += $(tds[i]).attr('colspan') || 1;
        }
    },

    /**
     * @param {*} model 
     * @param {*} diff 
     */
    on_change_col_span(model, diff) {

        // get diff
        var prev = parseInt(diff.prev || 0);
        var now = parseInt(diff.now || 0);

        // get the tr
        let $tr = this.$el.closest('tr');
        let row_index = $tr.index();
        let $table = $tr.closest('table');
        let $el = model.view.$el;
        let index = $el.index();
        let rowspan = parseInt($el.attr('rowspan')) || 1;

        var diff = now - prev;
        if (diff > 0) {
            // remove cell
            for (let i = 0; i < rowspan; i++) {
                $tr = $table.find('tr').eq(row_index + i);
                let tmp_index = index;
                let tmp_diff = diff;
                if (i == 0 || rowspan > 1) {
                    tmp_index += 1;
                    if (prev == 0) {
                        tmp_diff -= 1;
                    }
                }
                while (tmp_diff > 0) {
                    // get the next td
                    let $td = $tr.find('td').eq(tmp_index);
                    if (!$td.length) {
                        break;
                    }
                    let model = $td.data('model');
                    // remove
                    model.remove();
                    // as we remove the cell, the index keeps the same
                    // tmp_index += 1;
                    tmp_diff--;
                }
            }
        } else {
            let new_cells = [];
            // add cell 
            while (diff++ < 0) {
                // get the next td
                let model = $tr.data('model');
                let cell_info = {
                    type: 'cell',
                    attributes: {}
                }
                if (rowspan > 1) {
                    cell_info.attributes.rowspan = rowspan;
                }
                // add
                let cells = model.append(cell_info, { at: now + index++ });
                new_cells = new_cells.concat(cells);
            }
            this.em.trigger('component:add_cells', new_cells);
        }
    },

    /**
     * @param {*} model 
     * @param {*} diff 
     */
    on_change_row_span(model, diff) {

        var prev = parseInt(diff.prev || 0);
        var now = parseInt(diff.now || 0);
        var diff = now - prev;

        let maxtrix = getTableMatrix(this.$el.closest('table'));
        let $td = model.view.$el;
        let colspan = parseInt($td.attr('colspan')) || 1;
        let $row = $td.closest('tr');
        let index = $row.index();
        let matrix_row = maxtrix[index];

        // get the col index
        let col_index = -1;
        for (let i = 0; i < matrix_row.length; i++) {
            if (matrix_row[i].cell === $td[0]) {
                col_index = i;
                break;
            }
        }

        if (diff > 0) {
            let next_index = index + prev;
            if (prev == 0) {
                next_index += 1;
                diff -= 1;
            }
            while (diff > 0) {
                let row = maxtrix[next_index]
                for (let j = 0; j < colspan; j++) {
                    let col_info = row[col_index + j];
                    let model = $(col_info.cell).data('model');
                    model.remove();
                }
                next_index++;
                diff--;
                if (next_index >= maxtrix.length) {
                    break;
                }
            }
        } else {
            var $table = $row.closest('table');
            var row_index = 0;
            let new_cells = [];
            // add cell 
            while (diff < 0) {
                // get the next td
                $row = $table.find('tr').eq(index + now + row_index);
                row_index++;
                let model = $row.data('model');``
                // check the row has the same colspan
                for (let i = 0; i < colspan; i++) {
                    let cell_info = {
                        type: 'cell',
                    }
                    let cells = model.append(cell_info, { at: col_index + i });
                    // append new cell
                    new_cells = new_cells.concat(cells);
                }
                diff++;
            }
            this.em.trigger('component:add_cells', new_cells);
        }
    },
});
