// new above
import { isArray } from 'underscore';
import { getTableMatrix, CELL_TYPE, isHolderCell } from 'utils/Table';

import Backbone from 'backbone';

let $ = Backbone.$;

export default {
  run(ed, sender, opts = {}) {

    let components = opts.component || ed.getSelectedAll();
    components = isArray(components) ? [...components] : [components];

    // component
    let component = this.em.getSelected()
    if (!component) {
      return
    }

    // matrix
    let view = component.view
    let $cell = $(view.el)
    let $table = $cell.closest('table')
    let matrix = getTableMatrix($table)

    // ajust the spans
    let el = view.el
    let $el = $(el)
    let $row = $el.closest('tr')
    let row_index = $row.index()
    let matrix_cells = matrix[row_index]
    for (let i = 0; i < matrix_cells.length; i++) {
      let cell_info = matrix_cells[i]
      // pre deal the cell info
      if (cell_info.type == CELL_TYPE.COL_SPAN) {
        // do nothing
        continue;
      } else if (cell_info.type == CELL_TYPE.ROW_SPAN) {
        let source_cell = cell_info.cell;
        source_cell.rowSpan = parseInt(source_cell.rowSpan) + 1;
      } else if (cell_info == CELL_TYPE.COL_ROW_SPAN) {
        let source_cell = cell_info.cell;
        source_cell.rowSpan = parseInt(source_cell.rowSpan) + 1;
      } else if (cell_info.type == CELL_TYPE.CELL && cell_info.rowSpan && cell_info.rowSpan > 1) {
        cell_info.colSpan = parseInt(cell_info.colSpan) || 1;
        cell_info.rowSpan = null;
      }
    }
    // append the row and the cells
    let cells = []
    for (let i = 0; i < matrix_cells.length; i++) {
      let cell_info = matrix_cells[i]
      if (cell_info.type == CELL_TYPE.CELL) {
        let cell = {
          'type': 'cell',
          'attributes': {}
        }
        if (cell_info.colSpan && cell_info.colSpan > 1) {
          cell.attributes.colSpan = cell_info.colSpan
        }
        cells.push(cell)
      }
    }

    // as the chidren will trigger add mesage, so we need to prevent it
    let $tbody = $table.find('tbody')
    if (!$tbody.length) {
      $tbody = $table
    }
    let $table_body_model = $tbody.data('model')
    let row_model = $table_body_model.append({
      type: 'row',
      components: cells
    }, {
      at: row_index,
    })
    ed.trigger('component:add_row', row_model);
  }
};
