import ComponentView from './ComponentView';
import { bindAll } from 'underscore';
import fa from '../../i18n/locale/fa';

const $ = Backbone.$;

export default ComponentView.extend({

    init() {
        this.rowHandlers = [];
        this.columnHandlers = [];
        this.dragging_type = undefined;

        bindAll(this, 'handleMouseDown', 'dragMove', 'dragStop');

        // this.$el.on('mouseenter', (e) => {
        //     // as we dragging, may it will triiger mouseenter event
        //     if (this.is_dragging) {
        //         return;
        //     }

        //     // if the element is handler
        //     let $target = $(e.relatedTarget);
        //     if ($target.hasClass('anita-row-handler')
        //         || $target.hasClass('anita-column-handler')) {
        //         return
        //     }

        //     this.createRowHandlers();
        //     this.createColumnHandlers();

        //     $(document).off('mousedown', this.handleMouseDown);
        //     $(document).on('mousedown', this.handleMouseDown);
        // });

        // // mouse out table
        // this.$el.on('mouseleave', (e) => {
        //     // if the element is handler
        //     let $target = $(e.relatedTarget);
        //     if ($target.hasClass('anita-row-handler')
        //         || $target.hasClass('anita-column-handler')) {
        //         return
        //     }
        //     this.removeRowHandlers();
        //     this.removeColumnHandlers();
        //     $(document).off('mousedown', this.handleMouseDown);
        // })

    },

    createRowHandlers() {
        // get rows
        var $rows = $(this.el).find('tr');

        // create the row handlers
        $rows.each((i, row) => {
            var $handler = this.createRowHander($(row), i);
            $handler.appendTo($('body'));
            this.rowHandlers.push($handler);
        });
    },

    createColumnHandlers() {

        if (this.columnHandlers.length != 0) {
            // remove all the column handlers
            this.removeColumnHandlers();
            return;
        }

        // get the first row
        var $rows = $(this.el).find('tr:first-child');
        var $tds = $rows.find('td')
        var maxtrix = this.getTableMatrix($(this.el));
        console.log(maxtrix)

        /**
         * get cell form matrix, as there has colspan and row span
         * @param {*} index 
         * @returns 
         */
        function getCellFromMatrix(index) {
            for (var j=0; j < maxtrix.length; j++) {
                var maxtrix_row = maxtrix[j];
                for (var k = 0; k < maxtrix_row.length; k++) {
                    if (index > maxtrix_row.length) {
                        // log error
                        console.log('error, the index is out of range');
                        // print the stack
                        console.log(new Error().stack);
                    }
                    var cell = maxtrix_row[index];
                    if (cell) {
                        let rowspan = cell.getAttribute('rowspan');
                        let colspan = cell.getAttribute('colspan');
                        if (rowspan && parseInt(rowspan) > 1) {
                            continue;
                        }
                        if (colspan && parseInt(colspan) > 1) {
                            continue;
                        }
                        return $(cell);
                    }
                }
            }
            return false;
        }

        // create the row handlers
        var index = 0;
        $tds.each((i, td) => {
            // check colspan
            var colspan = $(td).attr('colspan');
            if (colspan) {
                for (var j = 0; j < colspan; j++) {
                    var $td = getCellFromMatrix(index);
                    if (!$td ) {
                        continue;
                    }
                    var $handler = this.createColumnHander($td, index);
                    $handler.appendTo($('body'));
                    this.columnHandlers.push($handler);
                    index++;
                }
            } else {
                // check the clomn handler has been created
                var $handler = this.createColumnHander($(td), index);
                $handler.appendTo($('body'));
                this.columnHandlers.push($handler);
                index++;
            }
        });
    },

    render(...args) {
        ComponentView.prototype.render.apply(this, args);
        return this;
    },

    getBoundingRect(el) {
        var w = window;
        var rect = el.getBoundingClientRect();
        return {
            left: rect.left + w.pageXOffset,
            top: rect.top + w.pageYOffset,
            width: rect.width,
            height: rect.height
        };
    },

    createRowHander($row, index) {

        var $handler = this.$el.find(`.anita-row-handler[data-row-index='${index}']`)
        if ($handler.length == 0) {
            $handler = $('<div class="anita-row-handler"></div>');
            $handler.data('row-index', index);
            $handler.attr('data-row-index', index);
            $row.data('handler', $handler);
            $handler.css('height', 5);
        }

        // get $row rect
        var rect = this.getBoundingRect($row[0]);

        $handler.css('top', rect.top + rect.height - 2);
        $handler.css('left', rect.left);
        $handler.css('width', rect.width);
        $handler.css('height', 5);

        return $handler;
    },

    createColumnHander($td, index) {

        var $handler = this.$el.find(`.anita-column-handler[data-column-index='${index}']`)
        if ($handler.length == 0) {
            var $handler = $('<div class="anita-column-handler"></div>');
            $handler.data('column-index', index)
            $handler.attr('data-column-index', index);
            $td.data('handler', $handler);
        }

        // get table rect
        var rect1 = this.getBoundingRect(this.el);

        // get $row rect
        var rect2 = this.getBoundingRect($td[0]);

        $handler.css('top', rect1.top);
        $handler.css('left', rect2.left + rect2.width - 2);
        $handler.css('width', 5);
        $handler.css('height', rect1.height);

        return $handler;
    },

    isRowHander(el) {
        var handlers = this.rowHandlers;

        for (var n in handlers) {
            if (handlers[n].get(0) === el) {
                return true;
            }
        }

        return false;
    },

    isColumnHander(el) {
        var handlers = this.columnHandlers;

        for (var n in handlers) {
            if (handlers[n].get(0) === el) {
                return true;
            }
        }

        return false;
    },

    startDrag(e) {

        // Right or middel click
        if (e.button !== 0) {
            return;
        }

        this.is_dragging = true;

        this.startPos = {
            x: e.clientX,
            y: e.clientY
        };

        this.selectedHandler.addClass('dragging');

        $(document).on('mousemove', this.dragMove);
        $(document).on('mouseup', this.dragStop);
    },

    dragStop(e) {
        this.is_dragging = false;

        // get the delta position
        var delta = {
            x: e.clientX - this.startPos.x,
            y: e.clientY - this.startPos.y
        };

        // if drag row
        if (this.dragging_type == 'row') {
            this.updateRowHeight(delta, true);
        } else if (this.dragging_type == 'column') {
            this.updateColumnWidth(delta, true);
        }

        $(document).off('mousemove', this.dragMove);
        $(document).off('mouseup', this.dragStop);

        // check mouse is in the table
        var x = e.clientX;
        var y = e.clientY;
        var table_rect = this.getBoundingRect(this.el);
        if (x < table_rect.left 
            || x > table_rect.left + table_rect.width 
            || y < table_rect.top 
            || y > table_rect.top + table_rect.height) {
            this.removeRowHandlers();
            this.removeColumnHandlers();
        } else {
            this.updateRowHandlers();
            this.updateColumnHanders();
        }
    },

    dragMove(e) {

        e.preventDefault();
        e.stopPropagation();

        // get the delta position
        var delta = {
            x: e.clientX - this.startPos.x,
            y: e.clientY - this.startPos.y
        };

        // if drag row
        if (this.dragging_type == 'row') {
            this.updateRowHeight(delta);
        } else if (this.dragging_type == 'column') {
            this.updateColumnWidth(delta);
        }

        this.startPos = {
            x: e.clientX,
            y: e.clientY
        };
    },

    updateSelectCell(cell) {
        // unselect all the cell
        $(this.el).find('td').removeClass('selected');

        var startCell = this.startCell;
        var endCell = cell;
        var startRow = startCell.parentNode;
        var endRow = endCell.parentNode;
        var start_row_index = startRow.rowIndex;
        var end_row_index = endRow.rowIndex;
        var startCellIndex = startCell.cellIndex;
        var endCellIndex = endCell.cellIndex;
        var max_cell_index = Math.max(startCellIndex, endCellIndex);
        var min_cell_index = Math.min(startCellIndex, endCellIndex);
        var rows = this.el.querySelectorAll('tr');
        for (var i = start_row_index; i <= end_row_index; i++) {
            var row = rows[i];
            var cells = row.querySelectorAll('td');
            for (var j = min_cell_index; j <= max_cell_index; j++) {
                var cell = cells[j];
                cell.classList.add('selected');
            }

            // the start cell
            var tmp_start_cell = cells[min_cell_index];
            var start_row_index = this.getColSpanStartIndex(tmp_start_cell);
            if (start_row_index != -1) {
                var start_cell = cells[start_row_index];
                var span = int(start_cell.getAttribute('colspan'));
                for (var j = start_row_index; j <= start_row_index + span; j++) {
                    var cell = cells[j];
                    cell.classList.add('selected');
                }
            }
            // the end cell
            var tmp_end_cell = cells[max_cell_index];
            var end_span_index = this.getColSpanEndIndex(tmp_end_cell);
            if (end_span_index != -1 && end_span_index != start_row_index) {
                var end_cell = cells[end_span_index];
                var span = int(end_cell.getAttribute('colspan'));
                for (var j = end_span_index; j <= end_span_index + span; j++) {
                    var cell = cells[j];
                    cell.classList.add('selected');
                }
            }
        }

        // check row span from first row
        var $all_rows = this.el.querySelectorAll('tr');
        var row = rows[start_row_index];
        var cells = row.querySelectorAll('td');
        for (var j = 0; j <= cells.length; j++) {
            var cell = cells[j];
            var start_row_index = this.getRowSpanStartIndex(cell);
            if (start_row_index != -1) {
                var start_row = $all_rows[start_row_index];
                var start_row_cells = start_row.querySelectorAll('td');
                var start_cell = start_row_cells[j];
                var span = int(start_cell.getAttribute('rowspan'));
                for (var k = start_row_index; k <= start_row_index + span; k++) {
                    var row_cells = $all_rows[k].querySelectorAll('td');
                    var tmp_cell = row_cells[j];
                    tmp_cell.classList.add('selected');
                }
            }
        }

        // check row span form last row
        var row = rows[end_row_index];
        var cells = row.querySelectorAll('td');
        for (var j = 0; j <= cells.length; j++) {
            var cell = cells[j];
            var end_row_index = this.getRowSpanEndIndex(cell);
            if (end_row_index != -1) {
                var end_row = $all_rows[end_row_index];
                var end_row_cells = end_row.querySelectorAll('td');
                var end_cell = end_row_cells[j];
                var span = int(end_cell.getAttribute('rowspan'));
                for (var k = end_row_index; k <= end_row_index + span; k++) {
                    var row_cells = $all_rows[k].querySelectorAll('td');
                    var tmp_cell = row_cells[j];
                    tmp_cell.classList.add('selected');
                }
            }
        }
    },

    getRowSpanStartIndex(td) {
        var col_index = td.cellIndex;
        var row_index = td.parentNode.rowIndex;
        var row = td.parentNode;
        var min_span = 1;
        for (var i = row_index; i >= 0; i--) {
            var row = rows[i];
            var cells = row.querySelectorAll('td');
            var cell = cells[col_index];
            var rowspan = int(cell.getAttribute('rowspan'));
            if (rowspan > min_span) {
                return i;
            }
            if (i != row_index) {
                min_span++;
            }
        }
        return -1;
    },

    getColSpanStartIndex(td) {
        var index = td.cellIndex;
        var row = td.parentNode;
        var cells = row.querySelectorAll('td');
        var min_span = 1;
        for (var i = index; i > 0; i--) {
            var cell = cells[i];
            if (cell.colSpan > min_span) {
                return i;
            }
            if (i != index) {
                min_span++;
            }
        }
        return -1;
    },

    updateRowHeight(delta, trigger_event) {

        if (!this.selectedHandler) {
            return;
        }

        const style = {};

        var index = this.selectedHandler.data('row-index');
        var $row = $(this.el).find('tr').eq(index);
        var height = $row.height() + delta.y;
        $row.height(height);

        style['height'] = height + 'px';
        this.selectedHandler.css('top', this.selectedHandler.position().top + delta.y);

        // update the enxt row handlers
        var $rows = $(this.el).find('tr');
        for (var i = index + 1; i < $rows.length; i++) {
            this.updateRowHander($rows.eq(i), i);
        }

        if (trigger_event) {
            var model = $row.data('model');
            model.addStyle({ ...style }, { avoidStore: 0 });
            const updateEvent = 'update:component:table:height';
            this.em && this.em.trigger(updateEvent, model, null);
        }
    },

    updateRowHandlers() {
        // get rows
        var $rows = $(this.el).find('tr');

        // create the row handlers
        $rows.each((i, row) => {
            this.updateRowHander($(row), i);
        });
    },

    updateRowHander($row, index) {
        // get $row rect
        var rect = this.getBoundingRect($row[0]);
        var $handler = this.rowHandlers[index];
        if (!$handler) {
            return
        }
        $handler.removeClass('dragging');
        $handler.css('top', rect.top + rect.height - 2);
        $handler.css('left', rect.left);
        $handler.css('width', rect.width);
        $handler.css('height', 5);
        return $handler;
    },

    updateColumnHanders() {
        // get the first row
        var $rows = $(this.el).find('tr:first-child');
        var $tds = $rows.find('td')
        // create the row handlers
        $tds.each((i, td) => {
            this.updateColumnHander($(td), i);
        });
    },

    updateColumnHander($td, index) {
        // get self rect
        var rect1 = this.getBoundingRect(this.el);
        // get $row rect
        var rect2 = this.getBoundingRect($td[0]);

        var $handler = this.columnHandlers[index];
        if (!$handler) {
            return
        }
        $handler.removeClass('dragging');
        $handler.css('top', rect1.top);
        $handler.css('left', rect2.left + rect2.width);
        $handler.css('width', 5);
        $handler.css('height', rect1.height);

        return $handler;
    },

    updateColumnWidth(delta, trigger_event = false) {
        if (!this.selectedHandler) {
            return;
        }
        var index = this.selectedHandler.data('column-index');
        // get matrix of cells
        var maxtrix = this.getTableMatrix($(this.el));
        for (var i = 0; i < maxtrix.length; i++) {
            var row = maxtrix[i];
            if (index > row.length) {
                continue;
            }
            var $cell = row[index]? $(row[index]) : null;
            if (!$cell) {
                for (var j = index - 1; j >= 0; j--) {
                    var tmp_cell = row[j];
                    if (tmp_cell) {
                        $cell = $(tmp_cell);
                        break;
                    }
                }
            }
            if ($cell) {
                var width = $cell.width() + delta.x;
                $cell.width(width);

                if (trigger_event) {
                    var model = $cell.data('model');
                    model.addStyle({ width: (width + delta.x) + 'px' }, { avoidStore: 0 });
                    const updateEvent = 'update:component:table:width';
                    this.em && this.em.trigger(updateEvent, model, null);
                }
            }
        }

        this.selectedHandler.css('left', this.selectedHandler.position().left + delta.x);
    },

    handleMouseDown(e) {
        var el = e.target;
        if (this.isColumnHander(el)) {
            e.stopPropagation();
            e.preventDefault();
            this.selectedHandler = $(el);
            this.dragging_type = 'column';
            this.startDrag(e);
        } else if (this.isRowHander(el)) {
            e.stopPropagation();
            e.preventDefault();
            this.selectedHandler = $(el);
            this.dragging_type = 'row';
            this.startDrag(e);
        } else if (el !== this.el) {
            this.selectedHandler = '';
            this.resetDrag();
        } else if (e.ctrlKey && $(this.el).contains(el) && $(el).is('td')) {
            // if it is a td and ctrl key is pressed
            this.startSelectCell = $(el);
            this.dragging_type = 'cell';
        }
    },

    resetDrag() {
        // Rollback to initial dimensions
        this.startPos = {
            x: 0,
            y: 0,
        };
        this.dragging_type = undefined;
        this.selectedHandler = undefined;

        // remove row handlers
        this.removeRowHandlers();

        // remove column handlers
        this.removeColumnHandlers();
    },

    handleKeyDown(e) {
        if (e.keyCode === 27) {
            this.dragStop();
        }
    },

    removeRowHandlers() {
        for (var n in this.rowHandlers) {
            this.rowHandlers[n].remove();
        }
        this.rowHandlers = [];
    },

    removeColumnHandlers() {
        for (var n in this.columnHandlers) {
            this.columnHandlers[n].remove();
        }
        this.columnHandlers = [];
    },


    /**
     * get matrix index from a element
     * @param {*} table 
     * @returns 
     */
     getTableMatrix($table) {
        // get all the rows
        let matrix = [];
        let rows = $table.find('tr');
        for (let i = 0; i < rows.length; i++) {
            let row = rows[i];
            let cols = $(row).find('td');
            let row_matrix = [];
            for (let j = 0; j < cols.length; j++) {
                row_matrix.push(cols[j]);
                // check if the cell has a colspan
                let colspan = $(cols[j]).attr('colspan');
                if (colspan) {
                    for (let k = 1; k < colspan; k++) {
                        row_matrix.push(null);
                    }
                }
            }
            matrix.push(row_matrix);
        }

        // fixed the row span
        for (let i = 0; i < matrix.length; i++) {
            for (let j = 0; j < matrix[i].length; j++) {
                let rowspan = $(matrix[i][j]).attr('rowspan');
                if (rowspan) {
                    for (let k = 1; k < rowspan; k++) {
                        matrix[i + k].splice(j, 0, null);
                        // deal colspan
                        let colspan = $(matrix[i][j]).attr('colspan');
                        if (colspan) {
                            for (let l = 1; l < colspan; l++) {
                                matrix[i + k].splice(j + l, 0, null);
                            }
                        }
                    }
                }
            }
        }

        return matrix;
    }

});
