import Backbone from 'backbone';

var $ = Backbone.$;

export const CELL_TYPE = {
    COL_SPAN: 0x01,
    ROW_SPAN: 0x02,
    COL_ROW_SPAN: 0x03,
    CELL: 0x04
}

export const isHolderCell = (cell) => {
    return cell == CELL_TYPE.COL_SPAN || cell == CELL_TYPE.ROW_SPAN || cell == CELL_TYPE.COL_ROW_SPAN;
}

export const getRealIndex = (maxtrix_row, virutal_index) => {
    let count = 0;
    for (let i = virutal_index; i >= 0; i--) {
        let cell_info = maxtrix_row[i];
        if (cell_info.type === CELL_TYPE.CELL) {
            count++;
        }
    }
    return count > 0 ? count - 1 : 0;
}

/**
 * get matrix index from a element
 * @param {*} table 
 * @returns 
 */
export const getTableMatrix = ($table) => {

    // get all the rows
    let matrix = [];
    let rows = $table.find('tr');
    for (var i = 0; i < rows.length; i++) {
        let row = rows[i];
        let cols = $(row).find('td');
        let row_matrix = [];
        for (var j = 0; j < cols.length; j++) {
            let tmp_cell = cols[j];
            let colSpan = tmp_cell.colSpan;
            let rowSpan  = tmp_cell.rowSpan;
            // check if the cell has a colSpan
            row_matrix.push({
                type: CELL_TYPE.CELL,
                cell: tmp_cell,
                row_index: i,
                col_index: j,  
                related_cell_row_index: i,
                related_cell_col_index: j,
                rowSpan : rowSpan ? parseInt(rowSpan): null,
                colSpan: colSpan? parseInt(colSpan): null
            });
        }
        matrix.push(row_matrix);
    }

    for (var i = 0; i < rows.length; i++) {
        let row = rows[i];
        let cols = $(row).find('td');
        for (var j = 0; j < cols.length; j++) {
            let tmp_cell = cols[j];
            let colSpan = parseInt(tmp_cell.colSpan);
            let rowSpan  = parseInt(tmp_cell.rowSpan);
            if (colSpan > 1 && rowSpan > 1 ) {
                for (var k = 0; k < rowSpan ; k++) {
                    for (var l = 0; l < colSpan; l++) {
                        if (k === 0 && l === 0) {
                            continue;
                        }
                        matrix[i + k].splice(j + l, 0, {
                            type: CELL_TYPE.COL_ROW_SPAN,
                            cell: tmp_cell,
                            row_index: i + k,
                            col_index: j + l,
                            related_cell_row_index: i,
                            related_cell_col_index: j,
                            cell: cols[j],
                            rowSpan : null,
                            colSpan: null
                        });
                    }
                }
            } else if (colSpan > 1) {
                // add the place hodler cell
                for (var k = 1; k < colSpan; k++) {
                    matrix[i].splice(j + k, 0, {
                        type: CELL_TYPE.COL_SPAN,
                        cell: tmp_cell,
                        row_index: i,
                        col_index: j + k,
                        related_cell_row_index: i,
                        related_cell_col_index: j,
                        rowSpan : null,
                        colSpan: colSpan
                    });
                }
            } else if (rowSpan > 1) {
                // add the place hodler cell
                for (var k = 1; k < rowSpan && i + k < rows.length; k++) {
                    matrix[i + k].splice(j, 0, {
                        type: CELL_TYPE.ROW_SPAN,
                        cell: cols[j],
                        row_index: i + k,
                        col_index: j,
                        related_cell_row_index: i,
                        related_cell_col_index: j,
                        rowSpan : null,
                        colSpan: null
                    });
                }
            }
        }  
    }

    return matrix;
}