import Backbone from 'backbone';
import ButtonsView from './ButtonsView';

export default Backbone.View.extend({
  initialize(o) {
    const config = o.config || {};
    const model = this.model;
    this.config = config;
    this.pfx = config.stylePrefix || '';
    this.ppfx = config.pStylePrefix || '';
    this.buttons = model.get('buttons');
    this.className = this.pfx + 'panel';
    this.id = this.pfx + model.get('id');
    this.listenTo(model, 'change:appendContent', this.appendContent);
    this.listenTo(model, 'change:content', this.updateContent);
    this.listenTo(model, 'change:visible', this.toggleVisible);
    model.view = this;
  },

  /**
   * Append content of the panel
   * */
  appendContent() {
    this.$el.append(this.model.get('appendContent'));
  },

  /**
   * Update content
   * */
  updateContent() {
    this.$el.html(this.model.get('content'));
  },

  toggleVisible() {
    if (!this.model.get('visible')) {
      this.$el.addClass(`${this.ppfx}hidden`);
      return;
    }
    this.$el.removeClass(`${this.ppfx}hidden`);
  },

  attributes() {
    return this.model.get('attributes');
  },

  initResize() {
    const em = this.config.em;
    const editor = em ? em.get('Editor') : '';
    const resizable = this.model.get('resizable');

    if (editor && resizable) {
      var resz = resizable === true ? [1, 1, 1, 1] : resizable;
      var resLen = resz.length;
      var tc,
        cr,
        bc,
        cl = 0;

      // Choose which sides of the panel are resizable
      if (resLen == 2) {
        tc = resz[0];
        bc = resz[0];
        cr = resz[1];
        cl = resz[1];
      } else if (resLen == 4) {
        tc = resz[0];
        cr = resz[1];
        bc = resz[2];
        cl = resz[3];
      }

      var resizer = editor.Utils.Resizer.init({
        tc,
        cr,
        bc,
        cl,
        tl: 0,
        tr: 0,
        bl: 0,
        br: 0,
        appendTo: this.el,
        silentFrames: 1,
        avoidContainerUpdate: 1,
        prefix: editor.getConfig().stylePrefix,
        onEnd() {
          em && em.trigger('change:canvasOffset');
        },
        posFetcher: (el, { target }) => {
          const style = el.style;
          const config = resizer.getConfig();
          const keyWidth = config.keyWidth;
          const keyHeight = config.keyHeight;
          const rect = el.getBoundingClientRect();
          const forContainer = target == 'container';
          const styleWidth = style[keyWidth];
          const styleHeight = style[keyHeight];
          const width =
            styleWidth && !forContainer ? parseFloat(styleWidth) : rect.width;
          const height =
            styleHeight && !forContainer
              ? parseFloat(styleHeight)
              : rect.height;
          return {
            left: 0,
            top: 0,
            width,
            height
          };
        },
        ...resizable
      });
      resizer.blur = () => {};
      resizer.focus(this.el);
    }
  },

  render() {
    const $el = this.$el;
    const ppfx = this.ppfx;
    const cls = `${this.className} ${this.id} ${ppfx}one-bg ${ppfx}two-color`;
    $el.addClass(cls);

    this.toggleVisible();

    let model_id = this.model.get('id');
    if (model_id == 'views') {
      let svg = `<svg width="100%" viewBox="0 0 120 120" width="48px" height="48px" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" xml:space="preserve" xmlns:serif="http://www.serif.com/" style="fill-rule:evenodd;clip-rule:evenodd;stroke-linejoin:round;stroke-miterlimit:2;">
        <g transform="matrix(1,0,0,1,-320.52,-210.36)">
            <g transform="matrix(0,-1,-1,0,721.02,651.24)">
                <ellipse cx="416.13" cy="304.89" rx="8.67" ry="8.73" style="fill:url(#_Linear1);"/>
            </g>
            <g transform="matrix(1,0,0,1,0,46.17)">
                <path d="M335.98,264.24L335.98,229.59L320.52,229.59C321.73,242.52 327.16,254.69 335.98,264.24Z" style="fill:url(#_Linear2);"/>
            </g>
            <g transform="matrix(1,0,0,1,0,8.98)">
                <path d="M380.09,201.38C351.03,201.4 326.2,222.34 321.24,251L335.99,251L335.99,240.04C335.99,236.1 336.55,232.73 337.81,230.06C338.91,227.49 340.54,225.19 342.59,223.31C344.44,221.52 346.71,220.22 349.19,219.51C351.7,218.79 354.3,218.41 356.91,218.39L368.57,218.39L368.57,233.71L357.06,233.71C353.83,233.71 352.28,235.54 352.28,239.33L352.28,251.14L368.15,251.14L368.15,266.89L352.28,266.89L352.28,314.26C381.45,329.64 417.54,318.44 432.91,289.25C437.43,280.67 439.79,271.11 439.78,261.4C439.78,228.37 412.96,201.38 380.09,201.38ZM400.03,299.5L400.03,313C400.04,313.39 399.94,313.78 399.76,314.13L394.55,314.13L394.55,286.56C394.67,282.95 391.84,279.93 388.23,279.82C388.14,279.81 388.05,279.81 387.96,279.81C384.23,279.81 381.21,282.84 381.21,286.56L381.21,313.98L376.01,313.98C375.9,313.62 375.86,313.24 375.9,312.87L375.9,286.71C375.74,280.03 381.01,274.48 387.68,274.32C394.35,274.15 399.9,279.43 400.06,286.1C400.07,286.3 400.07,286.51 400.06,286.71L400.03,299.5ZM407.76,314.11L401.86,314.11L401.86,286.98C401.9,281.06 398.26,275.74 392.73,273.63C385.61,271 377.71,274.64 375.08,281.76C374.71,282.77 374.46,283.82 374.33,284.88C374.05,287.7 374.05,290.5 374.05,293.32L374.05,313.98L368.15,313.98L368.15,286.56C368.13,275.63 376.97,266.76 387.9,266.74L387.96,266.74C398.77,266.89 407.76,275.75 407.76,286.56L407.76,314.11ZM416.33,233.71C412.06,233.72 408.6,230.26 408.59,225.99C408.59,221.71 412.05,218.25 416.31,218.24C420.58,218.24 424.05,221.7 424.05,225.97L424.05,225.98C424.06,230.24 420.62,233.7 416.37,233.71L416.33,233.71Z" style="fill:url(#_Linear3);"/>
            </g>
        </g>
        <defs>
            <linearGradient id="_Linear1" x1="0" y1="0" x2="1" y2="0" gradientUnits="userSpaceOnUse" gradientTransform="matrix(-41.13,23.75,-23.75,-41.13,446.98,287.08)"><stop offset="0" style="stop-color:rgb(182,36,110);stop-opacity:1"/><stop offset="1" style="stop-color:rgb(182,36,110);stop-opacity:1"/></linearGradient>
            <linearGradient id="_Linear2" x1="0" y1="0" x2="1" y2="0" gradientUnits="userSpaceOnUse" gradientTransform="matrix(37.93,-141.57,141.57,37.93,361.19,294.86)"><stop offset="0" style="stop-color:rgb(3,167,211);stop-opacity:1"/><stop offset="0.81" style="stop-color:rgb(21,76,146);stop-opacity:1"/><stop offset="1" style="stop-color:rgb(21,76,146);stop-opacity:1"/></linearGradient>
            <linearGradient id="_Linear3" x1="0" y1="0" x2="1" y2="0" gradientUnits="userSpaceOnUse" gradientTransform="matrix(113.8,-424.714,424.714,113.8,323.25,473.624)"><stop offset="0" style="stop-color:rgb(21,76,146);stop-opacity:1"/><stop offset="0.06" style="stop-color:rgb(21,76,146);stop-opacity:1"/><stop offset="0.33" style="stop-color:rgb(3,167,211);stop-opacity:1"/><stop offset="0.6" style="stop-color:rgb(21,76,146);stop-opacity:1"/><stop offset="0.73" style="stop-color:rgb(21,76,146);stop-opacity:1"/><stop offset="1" style="stop-color:rgb(3,167,211);stop-opacity:1"/></linearGradient>
        </defs>
    </svg>
    `;
      let elSvg = document.createElement('div');
      elSvg.innerHTML = svg;
      elSvg.style.margin = '10px auto';
      $el.append(elSvg);
    }

    if (this.buttons.length) {
      var buttons = new ButtonsView({
        collection: this.buttons,
        config: this.config
      });
      $el.append(buttons.render().el);
    }

    $el.append(this.model.get('content'));
    return this;
  }
});
